<template>
  <div class="my-bought-container">
    <div class="album-box">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="getMyBoughtList"
      >
        <div
          @click="clickAlbum(album)"
          v-for="album in myBoughtList"
          :key="album._id"
        >
          <div class="content-area">
            <BaseListLayout
              :album="album"
            />
          </div>
          <div class="split-line-1" />
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import BaseListLayout from '@/components/homePageLayout/BaseListLayout.vue';
import { apiAlbumBuylist } from '@/service/api/program';
import { toAlbumPage } from '@/utils/proxyRouteSkip';
import Vue from 'vue';
import { Toast, List } from 'vant';

Vue.use(Toast).use(List);

export default {
  components: {
    BaseListLayout
  },
  
  data() {
    return {
      myBoughtList: [],
      finished: true,
      pageSize: 20,
      pageNum: 0,
      loading: false,
    }
  },

  created() {
    document.title = '我的购买';
  },

  mounted() {
    this.getMyBoughtList();
  },

  methods: {
    // todo 获取我已购买的专辑
    async getMyBoughtList() {
      const myBoughtResponse = await apiAlbumBuylist(this.pageNum, this.pageSize);
      if (!myBoughtResponse || myBoughtResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      this.myBoughtList = [...this.myBoughtList, ...myBoughtResponse.data];
      if (myBoughtResponse.data.length < this.pageSize) {
        this.finished = true;
      } else {
        this.finished = false;
        this.pageNum += 1;
      }
      this.loading = false;
    },

    clickAlbum(album) {
      toAlbumPage(album.chargeType, album.isBuy, album._id);
    }
  }
}
</script>

<style lang="scss" scoped>
.my-bought-container {
  width: 100%;
  background: #fff;

  .album-box {
    width: 100%;
    padding: 0 16px;
    box-sizing: border-box;

    .content-area {
      padding: 20px 0;
    }
  }

  .split-line-1 {
    width: 343px;
    height: 1px;
    background: rgba(233,233,233,1);
  }
}
</style>


